/* App.css */

/* Apply Times New Roman as the default font for all text */
h1, h2, h3, html, body {
  font-family: 'Open Sans', sans-serif;
  margin: 0; /* Optional: Reset margin to ensure full-width styling */
  padding: 0; /* Optional: Reset padding if necessary */
}

/* Apply font to all elements to ensure Open Sans is used consistently */
*, *::before, *::after {
  font-family: 'Open Sans', sans-serif;
  box-sizing: border-box; /* Optional: Ensure consistent box sizing */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
